body{margin:0;font-family:interstate, sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}html,body,main{margin:0;padding:0}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}

.container{display:grid;grid-template-columns:-webkit-min-content 1fr;grid-template-columns:min-content 1fr;grid-template-rows:1fr;grid-template-areas:"picker map";grid-gap:1rem;min-height:100vh}.container .picker{grid-area:picker}.container svg{grid-area:map}

.map,svg{max-width:100vw;max-height:100vh}.map text,.map path,.map circle,svg text,svg path,svg circle{transition:0.5s ease-in-out}.map .tp,svg .tp{cursor:pointer}

.picker{min-width:20rem;background:#222;color:#fff;display:flex;flex-flow:column nowrap;height:100%}.picker ul{list-style:none;margin:0;padding:0.5rem 0;flex:1 1 auto}.picker ul button{background:none;border:none;display:block;color:#fff;padding:0.5rem 1rem;width:100%;text-align:left;font-size:1.25rem;font-family:interstate, sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}.picker ul button:hover,.picker ul button:hover strong{color:#f32735 !important;transition:0.5s ease-in-out}.picker ul button.active.served,.picker ul button.active.served strong{transition:0.5s ease-in-out}.picker ul button.active.served strong{color:#f32735}.picker ul button.served,.picker ul button.served strong{transition:0.5s ease-in-out}.picker ul button.served strong{color:#0071ce}.picker ul button strong{margin-right:0.5rem}

.clear-button{transition:opacity 0.2s ease-in-out;background:#0071ce;color:#fff;margin:0 1rem 1rem 1rem;border:none;display:block;padding:0.5rem 1rem;font-size:1.25rem;font-family:interstate, sans-serif;font-weight:400;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}.clear-button:hover{text-decoration:underline}.clear-button:active,.clear-button:focus{background:#f32735;text-decoration:none}.clear-button:focus{outline-color:#f32735}

